apis.com/css2?family=Cabin&family=Open+Sans:wght@300&display=swap');\n\nbody {\n    font-family: \"SF Pro Text\",-apple-system,Helvetica,Arial,\"Segoe UI\",Roboto,sans-serif,\"Apple Color Emoji\",\"Segoe UI Emoji\",\"Segoe UI Symbol\";\n    font-weight: bold;\n    margin: 0%;\n    background-color: rgb(255, 255, 255);\n    color: black;\n    font-family: 'Open Sans';\n}\n.wardrobe-container {\n    font-family: \"SF Pro Text\",-apple-system,Helvetica,Arial,\"Segoe UI\",Roboto,sans-serif,\"Apple Color Emoji\",\"Segoe UI Emoji\",\"Segoe UI Symbol\";\n    font-weight: normal;\n    margin: 0%;\n    border-color: #e1e1e1;\n}\n.outfit-container {\n    @extend .wardrobe-container;\n}\na {\n    text-decoration: none;\n    text-decoration-color: #000000;\n}\n.nav-bar{\n    font-family: \"SF Pro Text\",-apple-system,Helvetica,Arial,\"Segoe UI\",Roboto,sans-serif,\"Apple Color Emoji\",\"Segoe UI Emoji\",\"Segoe UI Symbol\";\n    font-weight: normal;\n    background-color: white;\n    align-items: left;\n    padding: 15px 16px 10px 16px;\n    margin: 0px 0px 0px 0px;\n    min-height: 35px;\n    width: 100%;\n    border-bottom: 1px solid #c3c3c3;\n    border-radius: 0px;\n    text-decoration: none;\n    display: inline-flex;\n    justify-content: left;\n    position: sticky;\n    top: 0;\n    z-index: 200;\n}\n.header{\n    display: flex;\n    justify-content: space-between;\n    padding: 12px 0px 16px 20px;\n    margin-right: 1%;\n    font-size: 20px;\n    font-weight: 2000;\n    top: 0;\n}\n.waviy {\nposition: relative;\n\n}\n.waviy span {\nposition: relative;\ndisplay: inline-block;\nfont-size: 28px;\nfont-weight: 450;\ncolor: black;\ntext-transform: uppercase;\nanimation: flip 7s infinite;\nanimation-delay: calc(.5s * var(--i))\n}\n@keyframes flip {\n0%,80% {\n    transform: rotateY(360deg) \n}\n}\n.sticky-outfit {\n    padding-bottom: 0px;\n    margin-bottom: 0px;\n    background: linear-gradient(to bottom, rgb(232, 230, 230), white);\n    position: sticky;\n    top: 61px;\n    z-index: 100;\n    box-shadow: none;\n    transition: all 0.8s ease; /* Add a transition for the shadow */\n}\n.sticky-outfit.with-shadow {\n    box-shadow: 0px 15px 12px rgba(0, 0, 0, .25); /* Add shadow to the bottom */\n    border-color: white;\n  }\n.outfit-submit {\n    justify-content: space-between;\n    display: flex;\n    box-sizing: border-box;\n    border-color:#000000;\n    border-style: solid;\n    border-width: 1px;\n    white-space: nowrap;\n    width: 100%;\n    max-width: 250px;\n    margin: 0px 0px 9px 18px;\n    padding: 1.5px;\n}\n.clothingBox {\n    // height: ;\n    \n    margin-bottom: 0%;\n    margin-left: 0%;\n    margin-right: 0%;\n    // actual Grailed color\n    // border-bottom: 1px solid #e1e1e1;\n    //darker for clearer division\n    border-bottom: 1px solid #c3c3c3;\n    border-radius: 0px;\n}\n.my-outfit {\n    margin: 7px 0px 5px 18px;\n    padding: 0rem .8rem;\n    font-size: 16px;\n    font-weight: 100;\n    color: white;\n    background-color: #000000;\n    box-sizing: border-box;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    text-decoration: none;\n    letter-spacing: .03em;\n    line-height: 1.3;\n    font-family: \"Video\",monospace;\n    text-transform: uppercase;\n}\n.categoryHeaders {\n    background: linear-gradient(to bottom, rgb(229, 228, 228), white);\n\n    font-size: 20px;\n    margin: 0px 0px 7px 0px;\n    padding-top: 12px;\n    padding-bottom: 5px;\n    padding-left: 18px;\n    font-weight: bold;\n    display: flex;\n    align-items: left;\n}\n.outfit-name-and-delete {\n    margin: 0px 16px;\n    padding: 0px;\n    display: flex;\n    max-width: 898px;\n    width: 100%;\n    text-overflow: ellipses;\n    overflow: hidden;\n    white-space: nowrap;\n}\n.user-input-field {\n    margin-top: 7px;\n    padding: 2px;\n    border-style: solid;\n    border-width: 1px;\n    border-color: rgb(212, 212, 212);\n}\n.file-input {\n    margin-top: 7px;\n}\n.add-item-buttons-container